<template>
  <div class="my-like">
    <el-row>
           <el-button type="success" v-on:click="like" id="like" plain>点赞({{likenum}})</el-button>
    </el-row>
    
   </div>
</template>
<script>
import api from "../../assets/api.js"
export default {
  props:['likenum','id','islike'],
  data() {
    return {
      data:"",
    }
  },
  methods: {
    like(){
      api.AddLikeNum(this.id).then(res=>{    
        $("#like").css("color"," #fff").css("background-color","#67c23a").css("border-color","#67c23a;");
        if(res.data.return=="1")
        {
          this.likenum=this.likenum+1;
        }
      });
    },
    startlike(){
      if(Number(this.islike)==1){
           $("#like").css("color"," #fff").css("background-color","#67c23a").css("border-color","#67c23a;");
      }
    },
  },
  watch:{
    islike:function () {
    this.$nextTick(function () {
      this.startlike()
    })}
    },
}
</script>
<style>
.my-like{
  width:80%;margin: 0 auto;padding-bottom:1px; margin-bottom: 50px;    text-align: center;
}
#like{
      width: 100px;
    height: 50px;
    font-size: 1em;
}
</style>



